import React, { Component } from "react";
import "../assets/styles/container.css";

class Donate extends Component {
  render() {
    return (
      <section className="container__small container__center">
        <section className="container__apple ">
          <div class="screen">
            <div class="screen-header">
              <div class="screen-header-left">
                <div class="screen-header-button close"></div>
                <div class="screen-header-button maximize"></div>
                <div class="screen-header-button minimize"></div>
              </div>
            </div>
            <div class="screen-body">
              <div className="title">Donate</div>
              <div className="text">
              Bienvenido a mi sitio Web, si te sirvieron mis apuntes y te interesa colaborar, puedes ayudarme a traves de Paypal.
              </div>
              <form
                action="https://www.paypal.com/donate"
                method="post"
                target="_top"
              >
                <input
                  type="hidden"
                  name="hosted_button_id"
                  value="LAC8X3WGSJDKY"
                />
                <input
                  type="image"
                  src="https://www.oldpathschapel.com/wp-content/uploads/2020/03/paypal-donate-button.png"
                  border="0"
                  name="submit"
                  title="PayPal - The safer, easier way to pay online!"
                  alt="Donate with PayPal button"
                />
                <img
                  alt=""
                  border="0"
                  src="https://www.paypal.com/en_PE/i/scr/pixel.gif"
                  width="1"
                  height="1"
                />
              </form>
            </div>
          </div>
        </section>
      </section>
    );
  }
}

export default Donate;
