                                        <td>Ejemplo: Un proyecto para desarrollar un nuevo f√°rmaco puede tener m√∫ltiples
                                            entregas, tales como presentaciones precl√≠nicas, resultados de ensayos de Fase 1,
                                            resultados de ensayos de Fase 2, resultados de ensayos de Fase 3, registro y luego
                                            lanzamiento</td>
