import React from "react";
import { NavbarItems } from './NavbarItems';
import { Link} from "react-router-dom";
import '../assets/styles/navbar.css';

const Navbar = () => {

    function openMenu() {

        var menu = document.getElementById('menu');
        if (menu.classList.contains('visible')) {
            menu.classList.remove('visible');
        } else {
            menu.classList.add('visible');
        }
    };
    function clean_theme() {
        document.body.classList.remove(
            "theme-blue",
            "theme-light-blue",
            "theme-green",
            "theme-red",
            "theme-yellow",
            "theme-pink",
            "theme-purple",
            "theme-orange");
    }
    function changeTheme() {
        var selectColor = document.getElementById('color-theme');
        var colorValue = selectColor.options[selectColor.selectedIndex].value;

        clean_theme();
        document.body.classList.toggle("theme-" + colorValue);
        selectColor.style.backgroundColor = 'var(--' + colorValue + ')';
    }
    return (
            <nav className="navbar">
                <section className="navbar__container">

                    <div className="navbar__logo">
                        <Link to="/">Jelig</Link>
                    </div>

                    <div id='menu' className="navbar__items">
                        <ul className="">
                            {NavbarItems.map((item, index) => {
                                return (
                                    <li key={index}>
                                        <Link class={item.cName} to={item.url}>
                                            {item.tittle}
                                        </Link>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>

                    <div className="navbar__dark">
                        <p id="dark" className="theme-dark">üåó</p>
                    </div>
                    <div className="navbar__color">
                        <select id="color-theme" onChange={changeTheme}>
                            <option value="blue" className="th-blue">Blue</option>
                            <option value="light-blue" className="th-light-blue">Light Blue</option>
                            <option value="red" className="th-red">Red</option>
                            <option value="green" className="th-green">Green</option>
                            <option value="yellow" className="th-yellow">Yellow</option>
                            <option value="pink" className="th-pink">Pink</option>
                            <option value="purple" className="th-purple">Purple</option>
                            <option value="orange" className="th-orange" label="Orange">Orange</option>
                        </select>
                    </div>

                    <div className="navbar__icon">
                        <i className="fa fa-bars" onClick={openMenu}></i>
                    </div>



                </section>

            </nav>
            
    );
};

export default Navbar;