import React from "react";

const Basic1 = () => {
    return (
        <section class="container-medium">

            <div class="card">
                <h1>Create your own board</h1>
                <img class="card img" src="../assets/img/english/img1.jpg" alt="" />

                <h2>Tips</h2>
                <div class="grid-fr l4 m2 s1">
                    <div class="card subitem">
                        <h3>TedTalks</h3>
                        <p>Ideal for listening and reading. They are talks with transcripts or subtitles, it is very
                            complete and with multiple topics</p>

                    </div>
                    <div class="card subitem">
                        <h3>LyricsTraining</h3>
                        <p>Ideal for listening. Here you see music videos, you hear and you have to recognize words that are
                            said
                            in the video, it is very funny, like a game.</p>

                    </div>
                    <div class="card subitem">
                        <h3>NewsInLevels</h3>
                        <p>Ideal for Listening and reading. It is a source of up-to-date international news, which you can
                            listen
                            to and read. Very comfortable without a doubt great to practice.</p>

                    </div>
                    <div class="card subitem">
                        <h3>HelloTalk</h3>
                        <p>Ideal for Talk and listen. Here you can speak with natives directly and practice.
                            Tandem
                            Ideal for Talk and listen. Similar to the previous one</p>

                    </div>

                </div>
            </div>
        </section>
    );

}


export default Basic1;