import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useRouteMatch,
    useParams
  } from "react-router-dom";
import {Economy} from './Posts/EconomyData';
import {BasicEnglish} from './Posts/EnglishData';

class Post extends React.Component {
    constructor(props) {
        console.log(props);
        super(props);
    }
    render() {
        return (
            
            <div className="card-post">
                <div className="card-post__top">
                    <Link to={`/blog/${this.props.href_post}`}>
                        <img className="card-post__img" src={this.props.img} alt="" />
                    </Link>
                </div>
                <div className="card-post__info">
                    <div className="card-post__about">
                        <a href='/' className="card-post__tag">{this.props.tag}</a>
                    </div>                    
                    <h2 className="card-post__title">
                        <Link to={`/blog/${this.props.href_post}`}>{this.props.title}</Link>
                    </h2>
                    <div className="card-post__creator">
                        <a href={this.props.href_author}>{this.props.author}</a>
                    </div>
                </div>
            </div>
        );
    }
}


class PostList extends React.Component {
    render() {
        return (
            <div>
                <h1>ECONOMY</h1>
                <main class="container__slide">


                    <section class="" id="mySlick1">
                        
                            {Economy.map((v, id) => {
                                return <Post key={id} href_author={v.href_author} href_post={v.href_post} img={v.img} title={v.title} author={v.author} tag={v.tag} />
                            })}
                        
                        
                    </section>
                </main>

                <h1>BASIC ENGLISH</h1>
                <main class="container__slide">
                    

                    <section class="" id="mySlick1">
                        
                            {BasicEnglish.map((v, id) => {
                                return <Post key={id} href_author={v.href_author} href_post={v.href_post} img={v.img} title={v.title} author={v.author} tag={v.tag} />
                            })}
                        
                        
                    </section>
                </main>

            </div>


        )
    }
}


export default PostList;