import React from "react";
import PostList from '../components/Post';
import { BrowserRouter as Router, HashRouter, Link, Route, Switch, Redirect, useRouteMatch } from "react-router-dom";
import Basic1 from "./Blog/English/Basic/Basic1";
import Economy1 from "./Blog/Economy/Economy1";
import Economy2 from "./Blog/Economy/Economy2";
import Economy3 from "./Blog/Economy/Economy3";

export default function Blog({ children }) {
    return (
        <section className="container__large">
            <Router basename="/Jelig">
                <Switch>
                    <Route exact path="/blog" component={PostList}></Route>
                    <Route path="/blog/basic1" component={Basic1}></Route>
                    <Route path="/blog/economy1" component={Economy1}></Route>
                    <Route path="/blog/economy2" component={Economy2}></Route>
                    <Route path="/blog/economy3" component={Economy3}></Route>
                    <Route render={() => <h1>Not found Blog!</h1>} />
                </Switch>
                
            </Router>
            { children }
        </section>
    );
}


